let degree = 0
let current = 0
let pageSlider = new Swiper(".gallery",{
    wrapperClass: "gallery__wrapper",
    slideClass: "gallery-slide",

    direction: "vertical",

    slidesPerView: "auto",
    speed: 1000,

    parallax: true,



    watchOverflow: true,

    observer: true,
    observeParents: true,
    observeSlideChildren: true,

    keyboard: {
        enabled: true,
        onlyInViewport: true,
        pageUpDown: true
    },

    mousewheel: {
        sensitivity: 1,
    },

    init: false,

    on: {
        init: function () {
            renderGalleryNav(this.activeIndex)
        },
        slideChange: function () {
            renderGalleryNav(this.activeIndex)
        },
        slideChangeTransitionStart: function() {
            rotateBox()
        },
        resize: function () {

        }
    },
    breakpoints: {
        320: {
            allowTouchMove: true,
        },
        992: {
            allowTouchMove: false,
        },
    }
})

$("._next-slide").click(function () {
    let slidesCount = $(".gallery-slide").length
    if(pageSlider.activeIndex<slidesCount-1){
        pageSlider.slideTo(pageSlider.activeIndex+1,1000,false)
    }else{
        pageSlider.slideTo(0,1000,false)
    }
    rotateBox()
})

$("._prev-slide").click(function () {
    let slidesCount = $(".gallery-slide").length
    if(pageSlider.activeIndex === 0){
        pageSlider.slideTo(slidesCount-1,1000,false)
    }else{
        pageSlider.slideTo(pageSlider.activeIndex-1,1000,false)
    }
    rotateBox()
})

function rotateBox() {
    let degree = +$('.gallery-slide.swiper-slide-active').attr('data-degree')
    $(".gallery__box").css("transform",`rotate(-${degree}deg)`)
}

$('.gallery-slide').each(function (i,el) {
    $(el).attr('data-degree',degree)
    degree = degree + 11
})

function renderGalleryNav(activeIndex){
    let links = []
    let activeClass = ''
    $(".gallery-nav__links").addClass("_disable")

    setTimeout(()=>{
        $(".gallery-nav__link").remove()
        $(".gallery-nav__links").removeClass("_disable")
        $(".gallery__title").each(function (index,el) {
            let link = {
                title: $(el).html().trim(),
                index,
                class: index === activeIndex ? "_active" : ""
            }
            links.push(link)
        })

        if (activeIndex > 7){
            for(let i=0;i<activeIndex-7;i++){
                if(links.length > 17){
                    links.shift()
                }
            }

        }


        links.forEach(link => {
            $(".gallery-nav__links").append(`
                <div data-index="${link.index}" class="gallery-nav__link ${link.class}">
                    <span>${link.title}</span>
                </div>
            `)
        })


    },300)
}

$(document).on('click', '.gallery-nav__link', function(e) {
    let slideIndex = +$(this).attr("data-index")
    pageSlider.slideTo(slideIndex,1000,false)
    rotateBox()
})






const resizeHandlerSlider = () => {
    pageSlider.init()
}

resizeHandlerSlider()

window.addEventListener('resize', resizeHandlerSlider);


$('.gallery-slide:first-child').bind('wheel', function(e){
    let top = $(".gallery").offset().top
    let screenHeight = $(window).height();

    if (e.originalEvent.wheelDelta >= 0){
        $('body,html').animate({
            scrollTop: top - screenHeight
        }, 100);
    }
})

$('.gallery-slide:last-child').bind('wheel', function(e){
    let top = $(".gallery").offset().top
    let screenHeight = $(window).height();
    if (e.originalEvent.wheelDelta <= 0){
        $('body,html').animate({
            scrollTop: top + screenHeight
        }, 0);
    }
})

let gallery = document.querySelector('.gallery')
let firstSlideGallery = document.querySelector('.gallery-slide:first-child')
let lastSlideGallery = document.querySelector('.gallery-slide:last-child')

gallery.addEventListener('touchstart', handleTouchStartGallery, false);


let xDown = null;
let yDown = null;

function getTouches(evt) {
    return evt.touches || // browser API
        evt.originalEvent.touches; // jQuery
}

function handleTouchStart(evt) {
    const firstTouch = getTouches(evt)[0];
    xDown = firstTouch.clientX;
    yDown = firstTouch.clientY;
};

listenFirstSlideTouch()
listenlastSlideTouch()

function listenFirstSlideTouch() {
    firstSlideGallery.addEventListener('touchstart', handleTouchStart, false);
    firstSlideGallery.addEventListener('touchmove', handleTouchMove, false);

        function handleTouchMove(evt) {
        if (!xDown || !yDown) {
            return;
        }
        let xUp = evt.touches[0].clientX;
        let yUp = evt.touches[0].clientY;
        let xDiff = xDown - xUp;
        let yDiff = yDown - yUp;
        if (Math.abs(xDiff) > Math.abs(yDiff)) { /*most significant*/

        } else {
            if (yDiff > 0) {

            } else {
                let top = $(".gallery").offset().top
                let screenHeight = $(window).height();
                $('body,html').animate({
                    scrollTop: top - screenHeight
                }, 100);
            }
        }
        /* reset values */
        xDown = null;
        yDown = null;
    };
}

function listenlastSlideTouch() {
    lastSlideGallery.addEventListener('touchstart', handleTouchStart, false);
    lastSlideGallery.addEventListener('touchmove', handleTouchMove, false);


    function handleTouchMove(evt) {
        if (!xDown || !yDown) {
            return;
        }
        let xUp = evt.touches[0].clientX;
        let yUp = evt.touches[0].clientY;
        let xDiff = xDown - xUp;
        let yDiff = yDown - yUp;
        if (Math.abs(xDiff) > Math.abs(yDiff)) { /*most significant*/

        } else {
            if (yDiff > 0) {
                let top = $(".gallery").offset().top
                let screenHeight = $(window).height();
                $('body,html').animate({
                    scrollTop: top + screenHeight
                }, 0);
            }
        }
        /* reset values */
        xDown = null;
        yDown = null;
    };
}



function handleTouchStartGallery() {
    galleryFullScreen()
};

$(".gallery").click(function () {
    galleryFullScreen()
})

$('.gallery').mouseenter(function () {
    galleryFullScreen()
})

function galleryFullScreen() {
    let footer = document.querySelector('.footer').getBoundingClientRect()

    let wrapperHeight = $(".wrapper").height();
    let footerHeight = footer.height;
    let screenHeight = $(window).height();

    console.log(footer)
    $('body,html').animate({
        scrollTop: wrapperHeight - footerHeight - screenHeight
    }, 100);
}
