$(window).scroll(function(){
    setFixHeader()
});

setFixHeader()


function setFixHeader() {
    if ($(window).scrollTop() > 20) {
        $(".header").addClass("_fix")
    } else {
        $(".header").removeClass("_fix")
    }
}

$(".burger").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})


$(".nav__link").click(function(event) {
    event.preventDefault();
    var idc = $(this).attr('href'),
        top = $(idc).offset().top;
    $('body,html').animate({ scrollTop: top - 140 }, 500);
    if ($(window).innerWidth() < 992) {
        $(".header").removeClass("_open")
    }
});
$(window).scroll(function() {
    var $sections = $('._anchor');
    $sections.each(function(i, el) {
        var top = $(el).offset().top - 175;
        var bottom = top + $(el).height();
        var scroll = $(window).scrollTop();
        var idn = $(el).attr('id');
        if (scroll > top && scroll < bottom) {
            $('a.nav__link').removeClass('_active');
            $('a[href="#' + idn + '"]').addClass('_active');
        }
    });
});
